body {
    /* margin: 0;
    padding: 0; */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
    /* margin: 0 10px; */
    /* padding: 0; */
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
    border-radius: 5px;
}

.logo-container img{
    height: 60px;
    width: 60px;
    align-items: center;
}

.nav-items > ul {
    display: flex;
    list-style-type: none;
    li{
        padding:10px;
        margin: 10px;
    }
}

.res-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.res-card{
    margin: 10px;
    padding: 10px;
    width: 200px;
    border-radius: 5px;
    h3, h4{
        margin: 0;
    }
}

.res-card:hover{
    border: 1px solid black;
    cursor: pointer;
}

.search-bar{
    padding:10px    
}

.res-logo{
    width: 100%;
    height: 200px;
}

.filter-btn{
    padding: 3px;
    margin: 5px;
}

.shimmer-container{
    display: flex;
    flex-wrap: wrap;
}

.shimmer-card{
    margin: 10px;
    width: 200px;
    height: 400px;
    background-color: #f0f0f0;
    border-radius: 5px;
}

.top-res{
    display: flex;
    justify-content: space-between;
    margin: 5px;

}



